import meter1 from "../assets/img/icons8-html.svg";
import meter2 from "../assets/img/icons8-css.svg";
import meter3 from "../assets/img/icons8-js.svg";
import meter4 from "../assets/img/icons8-react.svg";
import meter5 from "../assets/img/icons8-python.svg";
import meter6 from "../assets/img/icons8-c.svg";
import meter7 from "../assets/img/icons8-filmora.svg";
import meter8 from "../assets/img/icons8-ms-excel.svg";
import meter9 from "../assets/img/icons8-power-bi-2021.svg";
import meter10 from "../assets/img/icons8-figma.svg";
import meter11 from "../assets/img/icons8-nodejs.svg";
import meter12 from "../assets/img/icons8-express-js.svg";
import meter13 from "../assets/img/icons8-linux.svg";
import meter14 from "../assets/img/icons8-mongodb.svg";
import meter15 from "../assets/img/icons8-mysql.svg";
import Carousel from 'react-multi-carousel';
import 'react-multi-carousel/lib/styles.css';
// import arrow1 from "../assets/img/arrow1.svg";
// import arrow2 from "../assets/img/arrow2.svg";
import colorSharp from "../assets/img/color-sharp.png"

export const Skills = () => {
  const responsive = {
    superLargeDesktop: {
      // the naming can be any, depends on you.
      breakpoint: { max: 4000, min: 3000 },
      items: 5
    },
    desktop: {
      breakpoint: { max: 3000, min: 1024 },
      items: 3
    },
    tablet: {
      breakpoint: { max: 1024, min: 464 },
      items: 2
    },
    mobile: {
      breakpoint: { max: 464, min: 0 },
      items: 1
    }
  };

  return (
    <section className="skill" id="skills">
        <div className="container">
            <div className="row">
                <div className="col-12">
                    <div className="skill-bx wow zoomIn">
                        <h2>Skills</h2>
                        <p>Technologies, programming languages, frameworks and databases frequently used by me.</p>
                        <Carousel responsive={responsive} infinite={true} className="owl-carousel owl-theme skill-slider">
                            <div className="item">
                                <img src={meter1} alt="altImage" />
                            </div>
                            <div className="item">
                                <img src={meter2} alt="altImage" />
                            </div>
                            <div className="item">
                                <img src={meter3} alt="altImage" />
                            </div>
                            <div className="item">
                                <img src={meter4} alt="altImage" />
                            </div>
                            <div className="item">
                                <img src={meter5} alt="altImage" />
                            </div>
                            <div className="item">
                                <img src={meter6} alt="altImage" />
                            </div>
                            <div className="item">
                                <img src={meter7} alt="altImage" />
                            </div>
                            <div className="item">
                                <img src={meter8} alt="altImage" />
                            </div>
                            <div className="item">
                                <img src={meter9} alt="altImage" />
                            </div>
                            <div className="item">
                                <img src={meter10} alt="altImage" />
                            </div>
                            <div className="item">
                                <img src={meter11} alt="altImage" />
                            </div>
                            <div className="item">
                                <img src={meter12} alt="altImage" />
                            </div>
                            <div className="item">
                                <img src={meter13} alt="altImage" />
                            </div>
                            <div className="item">
                                <img src={meter14} alt="altImage" />
                            </div>
                            <div className="item">
                                <img src={meter15} alt="altImage" />
                            </div>
                        </Carousel>
                    </div>
                </div>
            </div>
        </div>
        <img className="background-image-left" src={colorSharp} alt="altImage" />
    </section>
  )
}
